/* Welcome Section */
.welcome-section {
    height: 110vh;
    padding: 5% 0;
    background-position: top center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    max-width: 100%;
    box-sizing: border-box;
    position: relative;
}

.welcome-section h1 {
    font-size: 5vw;
    font-weight: 700;
    letter-spacing: 0.8px;
    line-height: 1;
    margin-bottom: 2vw;
    display: block;
    color: white;
    margin-left: 5%;
    width: 100%;
    position: absolute;
    top: 10%; /* Dystans od góry na poziomie navbara */
}

.welcome-section p {
    color: #B8B8B8;
    font-size: 1.5vw;
    letter-spacing: 0.8px;
    line-height: 1.5em;
    text-align: justify;
    max-width: 50%;
    padding: 2vw;
    margin-left: 3%;
    border-radius: 25px;
    background-color: rgba(0, 0, 0, 0.7);
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.5);
    position: absolute;
    top: calc(25% + 5vw);
}

.welcome-section img {
    position: absolute;
    right: 0;
    top: 38%;
    width: 65%;
    max-width: 100%;
    z-index: -1;
}

@media screen and (max-width: 768px) {
    .welcome-section h1 {
        font-size: 15vw;
    }
    
    .welcome-section p {
        font-size: 5vw;
        max-width: 100%;
        top: calc(35% + 5vw);
    }
    .welcome-section img {
        position: absolute;
        right: 0;
        top: 70%;
        width: 100%;
        max-width: 100%;
        z-index: -1;
    }
}

/* Rest of the sections */
.section {
    height: 100vh;
    padding: 5% 0;
    background-position: top center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    max-width: 100%;
    box-sizing: border-box;
}

/* Nav Bar */
body {
    min-height: 100vh;
    background: black;
    margin: 0;
    padding: 0;
}

header {
    width: 100%;
    height: 10vh;
}

menu {
    list-style: none;
    padding: 0;
    margin: 0;
}
nav {
    height: 80%;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background: rgba(0, 0, 0, 0.9);
}

nav ul {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    list-style: none;
    text-decoration: none;
    padding: 0;
}

nav ul li {
    list-style-type: none;
    list-style: none;
    text-decoration: none;
    color: #fff;
    border-radius: 25px;
    font-size: 20px;
    padding: 6%;
    margin: 0 0px;
    transition: background 0.3s ease, color 0.3s ease, margin-right 0.3s ease;
}

nav ul li:hover,
nav ul li.active {
    background: #fff;
    color: black;
    margin-right: 20px;
}

.navbar-container {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 0px;
    z-index: 9999;
    transition: background-color 0.3s ease;
    margin-bottom: 10vh;
}

.black-bg {
    background-color: #000000;
    color: #ffffff;
}

.social-icon {
    display: inline-flex;
    align-items: center;
    margin-left: 120px;
    margin-right: 20px;
}

.social-icon a {
    width: 42px;
    height: 42px;
    background: rgba(255, 240, 219, 0.3);
    display: flex;
    border-radius: 50%;
    margin-right: 6px;
    align-items: center;
    justify-content: center;
    line-height: 1;
    border: 1px solid rgba(32, 31, 31, 0.5);
}

.social-icon a::before {
    content: "";
    width: 42px;
    height: 42px;
    position: absolute;
    background-color: #ffffff;
    border-radius: 50%;
    transform: scale(0);
    transition: 0.3s ease-in-out;
}

.social-icon a:hover::before {
    transform: scale(1);
}

.social-icon a img {
    width: 40%;
    z-index: 1;
    transition: 0.3s ease-in-out;
}

.social-icon a:hover img {
    filter: brightness(0) saturate(100%) invert(0%) sepia(7%) saturate(98%) hue-rotate(346deg) brightness(95%) contrast(86%);
}

@media (max-width: 768px) {
    .social-icon a {
        width: 32px;
        height: 32px;
    }
    nav ul li {
        font-size: 10px;
    }
    .social-icon {
        margin-left: 20px;
        margin-right: 0px;
    }
    nav ul li {
        padding: 3%;
    }
    nav ul li:hover,
    nav ul li.active {
        margin-right: 10px;
    }
}

/* Work Section */
.work-section {
    height: 100vh;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    overflow: hidden;
    margin-left: -20%;
    position: relative;
}

.work-section img {
    position: absolute;
    left: -10%;
    margin-top: -53vh;
    transform: scale(1);
    max-width: 400%;
    z-index: -1;
}

.work-content {
    width: 80%;
    margin-left: 60%;
}

@media (min-width: 769px) and (max-width: 1300px) {
    .work-section{
        height: 130vh;
    }

    .work-section img {
        position: absolute;
        left: -10%;

    }
    .work-content {
        width: 100%;
        margin-left: 50%;
    }
}

@media screen and (max-width: 768px) {
    .work-section{
        height: 130vh;
    }

    .work-section img {
        position: absolute;
        margin-top: auto;
        left: -30%;
        top: 27.5%;
        width: 150%;
    }
    .work-content {
        width: 100%;
        margin-left: 25%;
    }
}

/* Work Section Carousel */
body {
    display: flex;
}

* {
    box-sizing: border-box;
}

.carousel {
    position: relative;
    top: -15vh;
    max-width: 100%;
    width: 35rem;
    perspective: 500px;
    transform-style: preserve-3d;
}

.card-container {
    position: absolute;
    width: 100%;
    height: 100%;
    transform:
        rotateY(calc(var(--offset) * 50deg))
        scaleY(calc(1 + var(--abs-offset) * -0.4))
        translateZ(calc(var(--abs-offset) * -30rem))
        translateX(calc(var(--direction) * -5rem));
    filter: blur(calc(var(--abs-offset) * 1rem));
    transition: all 0.3s ease-out;
}

.card {
    width: 100%;
    padding: 2rem;
    background-color: hsl(120deg, 40%, calc(100% - var(--abs-offset) * 50%));
    border-radius: 1rem;
    color: #9ca3af;
    text-align: justify;
    transition: all 0.3s ease-out;
}

.card h2 {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    margin: 0 0 0.7em;
    color: #1f2937;
}

.card p {
    font-size: 1.2rem;
    line-height: 1em;
    margin: 0;
}

.card p,
.card h2 {
    transition: all 0.3s ease-out;
    opacity: var(--active);
}

.nav {
    color: rgb(255, 255, 255);
    font-size: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    margin-top: 25%;
    left: 20%;
    margin-right: 25%;
    margin-left: -25%;
    transform: translate(-50%, -50%);
    z-index: 999;
    cursor: pointer;
    user-select: none;
    background: unset;
    border: unset;
}

.nav.left {
    transform: translateX(-100%) translateY(-50%);
}
.nav.right {
    right: 0;
    transform: translateX(100%) translateY(-50%);
}

@media (min-width: 769px) and (max-width: 1300px) {
    .carousel {
        width: 70%;
        top: -20vh;
    }

    .nav {
        margin-top: 15%;
    }

    .card h2 {
        font-size: 1.5rem;
    }

    .card p {
        font-size: 0.9rem;
    }
}

@media screen and (max-width: 768px) {
    .carousel {
        width: 95%;
        left: -3%;
        top:auto;
    }

    .nav {
        color:rgba(150, 146, 143, 0.179);
        margin-top: 20%;
        left: 40%;
        margin-right: 30%;
        margin-left: -30%;
    }

    .card h2 {
        font-size: 1.5rem;
    }

    .card p {
        font-size: 0.9rem;
    }
}

/* Skills Section */
.technology-section {
    margin-left: 5%;
    background-color: #000;
    color: #fff;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: normal;
    padding: 20px;
}

.skills-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px;
}

.column {
    width: calc(50% - 10px);
    padding: 10px;
    margin-bottom: 20px;
    text-align: left;
    line-height: 1.4;
    font-size: 40px;
}

.column:nth-child(2) {
    margin-left: 0;
}

.additionally {
    width: 100%;
    padding-top: 20px;
}

h3 {
    font-size: 50px;
    margin-bottom: 20px;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

li {
    font-size: 25px;
    list-style: none;
    margin-bottom: 10px;
    position: relative;
    padding-left: 20px;
}

@media (max-width: 768px) {
    .technology-section {
        text-align: center;
        padding: 20px;
        height: 150vh;
    }

    .skills-container {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    .column {
        width: 100%;
        margin: 0;
        text-align: left;
        font-size: 24px;
        padding: 10px 0;
    }

    .column:nth-child(2) {
        margin-left: 0;
    }

    h3 {
        font-size: 30px;
        margin-bottom: 20px;
    }

    ul {
        list-style: none;
        padding: 0;
        margin: 0;
    }

    li {
        font-size: 20px;
        margin-bottom: 10px;
        position: relative;
        padding-left: 0px;
    }

    .additionally {
        margin-bottom: 10px;
        position: relative;
        padding-left: 0px;
        text-align: left;
    }
}

/* Projects Section */
.projects-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 80px 0;
    position: relative;
    background-color: rgb(0, 0, 0);
    text-align: center;
}

.projects-section h2 {
    font-size: 45px;
    font-weight: 700;
}

.projects-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
}

.proj-imgbx {
    position: relative;
    width: 520px;
    height: 380px;
    border-radius: 30px;
    overflow: hidden;
}

.proj-imgbx img {
    max-width: 100%;
    max-height: 100%;
}

.proj-imgbx::before {
    content: "";
    background: linear-gradient(90.21deg, #ddd5cd -5.91%, #776367 111.58%);
    opacity: 0.85;
    position: absolute;
    width: 100%;
    height: 0;
    transition: 0.4s ease-in-out;
}

.proj-imgbx:hover::before {
    height: 100%;
}

.proj-txtx {
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 0.5s ease-in-out;
    opacity: 0;
    width: 100%;
}

.proj-txtx a {
    text-decoration: none;
    color: white;
}

.proj-imgbx:hover .proj-txtx {
    top: 50%;
    opacity: 1;
}

.proj-txtx h4 {
    font-size: 30px;
    font-weight: 700;
    letter-spacing: 0.8px;
    line-height: 1.1em;
}

.proj-txtx span {
    font-style: italic;
    font-weight: 400;
    font-size: 15px;
    letter-spacing: 0.8px;
}

.background-image-right {
    top: 20%;
    position: absolute;
    bottom: 0;
    width: 35%;
    right: 0;
    z-index: -4;
}

@media (max-width: 768px) {
    .proj-imgbx {
        margin-left: 2.5%;
    }
    .proj-imgbx::before {
        /* Pozostała część stylu bez zmian */
        transition: 0.4s ease-in-out; /* Usuń ten transition */
    }
    
    /* Dodaj nowy styl z efektem po kliknięciu */
    .proj-imgbx.active::before {
        height: 100%;
    }
}
